import React from 'react';
import {Link, withRouter} from 'react-router-dom';

const Navbar = () => {
    return (
		<React.Fragment>
		
	<nav>
  <label htmlFor="drop" className="toggle">&#8801; Menu</label>
  <input type="checkbox" id="drop" />
  <ul className="menu">
    <li><Link to="/">Reception</Link></li>
    <li> 
      
      <label htmlFor="drop-2" className="toggle">Holiday Works +</label>
      <a href="">Holiday Works</a>
      <input type="checkbox" id="drop-2"/>
      <ul>
        <li className="dropdown-color"><Link to="/S1">Senior1</Link></li>
        <li className="dropdown-color"><Link to="/S2">Senior2</Link></li>
		<li  className="dropdown-color"><Link to="/S3">Senior3</Link></li>
        <li className="dropdown-color"> 
          
          
          <label htmlFor="drop-3" className="toggle">Senior4+</label>
          <Link href="">Senior4</Link>
          <input type="checkbox" id="drop-3"/>
          <ul>
            <li className="dropdown-color"><Link to="/S4MPC">S4 MPC</Link></li>
            <li className="dropdown-color"><Link to="/S4PCM">S4 PCM</Link></li>
            <li className="dropdown-color"><Link to="/S4MCB">S4 MCB</Link></li>
			<li className="dropdown-color"><Link to="/S4PCB">S4 PCB</Link></li>
          </ul>
        </li>
		<li className="dropdown-color"> 
          
          
          <label htmlFor="drop-3" className="toggle">Senior5 +</label>
          <Link>Senior 5</Link>
          <input type="checkbox" id="drop-3"/>
          <ul>
            <li className="dropdown-color"><Link to="/S5MPC">S5 MPC</Link></li>
            <li className="dropdown-color"><Link to="/S5PCM">S5 PCM</Link></li>
            <li className="dropdown-color"><Link to="/S5MCB">S5 MCB</Link></li>
			<li className="dropdown-color"><Link to="/S5PCB">S5 PCB</Link></li>
          </ul>
        </li>
		<li className="dropdown-color"> 
          
          
          <label htmlFor="drop-3" className="toggle">Senior6+</label>
          <Link>Senior 6</Link>
          <input type="checkbox" id="drop-3"/>
          <ul>
            <li className="dropdown-color"><Link to="/S6MPC">S6 MPC</Link></li>
            <li className="dropdown-color"><Link to="/S6PCM">S6 PCM</Link></li>
            <li className="dropdown-color"><Link to="/S6MCB">S6 MCB</Link></li>
			<li className="dropdown-color"><Link to="/S6PCB">S6 PCB</Link></li>
          </ul>
        </li>
      </ul>
    </li>
    <li><a href="/login.html">Join Live chat</a></li>
  </ul>
</nav>
    
	</React.Fragment>
    );
}

export default withRouter(Navbar);
