import React, { Component } from 'react';
import Works from '../Works';
import axios from 'axios';
class S6PCMwork extends Component {
    state = { 
            works:[],
            selectedWorks:[]
     }
     componentDidMount(){
        axios.get('/links')
        .then(res =>{
            let works = res.data;
            let selectedWorks = works.filter(work => work.class == 'S6PCM');
            return this.setState({works, selectedWorks});
        });

     }
    render() { 
        return (  
            <React.Fragment>
                <div className="container">
               <h1 className="text-center mt-5">Senior 6 PCM Holiday works</h1>
               <hr className="mt-5"/>
               <Works Works={this.state.selectedWorks}/>
               </div>
               
            </React.Fragment>
        );
    }
}
 
export default S6PCMwork;
