import React, { Component } from 'react';
import axios from 'axios';
import Works from '../Works'
class S6MPCwork extends Component {
    state = { 
        works:[],
        selectedWorks:[]
 }
 componentDidMount(){
    axios.get('/links')
    .then(res =>{
        let works = res.data;
        let selectedWorks = works.filter(work => work.class == 'S6MPC');
        return this.setState({works, selectedWorks});
    });
 }
    render() { 
        return (  
            <React.Fragment>
                <div className="container">
               <h1 className="text-center mt-5">Senior 6 MPC Holiday works</h1>
               <hr className="mt-5"/>
               <Works Works={this.state.selectedWorks}/>
               </div>
               
            </React.Fragment>
        );
    }
}
 
export default S6MPCwork;
