
import React from 'react';

const Works = ({Works}) => {
    const WorkList = Works.length ? (
        Works.map(work =>{
            return(
                <div className="text-center work-card" key={work._id}>
                    <h2>  {work.title} </h2>
            <p className="lead">Facilitator: {work.facilitator}</p>
            <h6>Upload Date: {work.date}</h6>
            <a href={work.link}><button className="workdownloadbtn">Download Work</button></a>
                </div>
            )
        })
    ):(
        <div className="container work-card">
            <h3 className="text-center m-2 no-works">You have no Holidays works Yet !</h3>
        </div>
    );
    return (
        <div>
            {WorkList}
        </div>
    );
}

export default Works;
