import React from 'react';
import Navbar from './components/Navbar';
import Welcome from './components/Welcome';
import S1work from './components/classes/S1';
import S2work from './components/classes/S2';
import S3work from './components/classes/S3';
import S4MCBwork from './components/classes/S4MCB';
import S4MPCwork from './components/classes/S4MPC';
import S4PCBwork from './components/classes/S4PCB';
import S4PCMwork from './components/classes/S4PCM';
import S5MCBwork from './components/classes/S5MCB';
import S5MPCwork from './components/classes/S5MPC';
import S5PCBwork from './components/classes/S5PCB';
import S5PCMwork from './components/classes/S5PCM';
import S6MPCwork from './components/classes/S6MPC';
import S6MCBwork from './components/classes/S6MCB';
import S6PCBwork from './components/classes/S6PCB';
import S6PCMwork from './components/classes/S6PCM';
import {BrowserRouter,Route, Switch} from 'react-router-dom';

import 'bootstrap/dist/css/bootstrap.css';
function App() {
  return (
    <BrowserRouter>
    <div className="App">
     <Navbar/>
     <Switch>
     <Route exact path="/" component={Welcome}/>
     <Route path="/S1" component={S1work}/>
     <Route path="/S2" component={S2work}/>
     <Route path="/S3" component={S3work}/>
     <Route path="/S4MPC" component={S4MPCwork}/>
     <Route path="/S4PCM" component={S4PCMwork}/>
     <Route path="/S4PCB" component={S4PCBwork}/>
     <Route path="/S4MCB" component={S4MCBwork}/>
     <Route path="/S5MPC" component={S5MPCwork}/>
     <Route path="/S5PCB" component={S5PCBwork}/>
     <Route path="/S5MCB" component={S5MCBwork}/>
     <Route path="/S5PCM" component={S5PCMwork}/>
     <Route path="/S6MCB" component={S6MCBwork}/>
     <Route path="/S6MPC" component={S6MPCwork}/>
     <Route path="/S6PCB" component={S6PCBwork}/>
     <Route path="/S6PCM" component={S6PCMwork}/>
     </Switch>
    </div>
    </BrowserRouter>
  );
}

export default App;
